import React, { useState, useEffect } from "react";
import Layout from "./Layout";
import { MDBContainer, MDBRow, MDBCol, MDBListGroup } from "mdbreact";
import Card from "./Card";
import { getCategories } from "./apiCore";
import Checkbox from "./Checkbox";

const Shop = () => {
  const [myFilters, setMyFilters] = useState({
    filters: { category: [], price: [] },
  });
  const [categories, setCategories] = useState([]);
  const [error, setError] = useState(false);

  const init = () => {
    getCategories().then((data) => {
      if (data.error) {
        setError(data.error);
      } else {
        setCategories(data);
      }
    });
  };

  useEffect(() => {
    init();
  }, []);

  const handleFilters = (filters, filterBy) => {
    // console.log("SHOP", filters, filterBy);
    const newFilters = { ...myFilters };
    newFilters.filters[filterBy] = filters;
    setMyFilters(newFilters);
  };

  return (
    <Layout
      title='Shop Page'
      description='Search and find products of your choice'
      className='container-fluid'
    >
      <MDBContainer fluid>
        <MDBRow>
          <MDBCol className='col-lg-3 col-md-6  mb-4'>
            <h4 className='font-weight-bold'>Filter by Categories</h4>
            <MDBListGroup style={{ height: "10px" }}>
              <Checkbox
                categories={categories}
                handleFilters={(filters) => handleFilters(filters, "category")}
              />
            </MDBListGroup>
          </MDBCol>

          <MDBCol className='col-lg-3 col-md-6 mb-4'>
            <h4 className='font-weight-bold text-center'>Products</h4>
            <div className='card'>{JSON.stringify(myFilters)}</div>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Layout>
  );
};

export default Shop;
