import React, { useState } from "react";
import Layout from "../core/Layout";
import { isAuthenticated } from "../auth/index";
import { Link } from "react-router-dom";
import { createCategory } from "./apiAdmin";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBIcon,
} from "mdbreact";

const AddCategory = () => {
  const [name, setName] = useState("");
  const [error, setError] = useState(false);
  const [success, setSuccess] = useState(false);

  // destructure user and info from localStorage
  const { user, token } = isAuthenticated();

  const handleChange = (e) => {
    setError("");
    setName(e.target.value);
  };

  const clickSubmit = (e) => {
    e.preventDefault();
    setError("");
    setSuccess(false);

    // make request to api to create new category
    createCategory(user._id, token, { name }).then((data) => {
      if (data.error) {
        setError(true);
      } else {
        setError("");
        setSuccess(true);
      }
    });
  };

  const newCategoryForm = () => (
    <MDBContainer>
      <MDBRow center>
        <MDBCol md='6'>
          <MDBCard>
            <MDBCardBody>
              <form onSubmit={clickSubmit}>
                <p className='h4 text-center py-4'>New Category</p>
                <label
                  htmlFor='defaultFormCardNameEx'
                  className='grey-text font-weight-light'
                >
                  Category Name
                </label>
                <input
                  type='text'
                  value={name}
                  onChange={handleChange}
                  autoFocus
                  placeholder='New Category'
                  id='defaultFormCardNameEx'
                  className='form-control'
                  required
                />
                <br />
                <div className='text-center py-4 mt-3'>
                  <MDBBtn className='btn btn-outline-indigo' type='submit'>
                    Create Category
                    <MDBIcon icon='plus' className='ml-2' />
                  </MDBBtn>
                </div>
                <div className='mt-5 text-center'>
                  <Link
                    to='/admin/dashboard'
                    className='text-warning'
                    style={{ textDecoration: "underline" }}
                  >
                    Back to Dashboard
                  </Link>
                </div>
              </form>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>
      </MDBRow>
    </MDBContainer>
  );

  const showSuccess = () => {
    if (success) {
      return (
        <div
          className='alert alert-success'
          style={{ display: success ? "" : "none" }}
        >
          {name} category has been created successfully.
        </div>
      );
    }
  };

  const showError = () => {
    if (error) {
      return (
        <div
          className='alert alert-danger'
          style={{ display: error ? "" : "none" }}
        >
          {name} category already exists, category name should be unique.
        </div>
      );
    }
  };

  return (
    <Layout
      title='Create A New Category'
      description={`G'day ${user.name}, ready to create a new category`}
      className='container-fluid'
    >
      <MDBContainer>
        {showSuccess()}
        {showError()}
        {newCategoryForm()}
      </MDBContainer>
    </Layout>
  );
};

export default AddCategory;
