import React from "react";
import Layout from "../core/Layout";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
  MDBCardTitle,
  MDBListGroup,
  MDBListGroupItem,
} from "mdbreact";
import { isAuthenticated } from "../auth";
import { Link } from "react-router-dom";

const AdminDashboard = () => {
  const {
    user: { name, email, role },
  } = isAuthenticated();

  const adminLinks = () => {
    return (
      <MDBContainer fluid>
        <MDBCol>
          <MDBCard>
            <MDBCardBody>
              <MDBListGroup>
                <MDBListGroupItem>
                  <MDBCardTitle>Admin Links</MDBCardTitle>
                </MDBListGroupItem>
                <MDBListGroupItem>
                  <Link to='/create/category'>Create Category</Link>
                </MDBListGroupItem>
                <MDBListGroupItem tag='li'>
                  <Link to='/create/product'>Create Product</Link>
                </MDBListGroupItem>
              </MDBListGroup>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>
      </MDBContainer>
    );
  };

  const adminInfo = () => {
    return (
      <MDBContainer fluid>
        <MDBCol>
          <MDBCard>
            <MDBCardBody>
              <MDBListGroup>
                <MDBListGroupItem>
                  <MDBCardTitle>Admin Information</MDBCardTitle>
                </MDBListGroupItem>
                <MDBListGroupItem>{name}</MDBListGroupItem>
                <MDBListGroupItem>{email}</MDBListGroupItem>
                <MDBListGroupItem>
                  {role === 1 ? "Admin" : "Registered User"}
                </MDBListGroupItem>
              </MDBListGroup>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>
      </MDBContainer>
    );
  };

  return (
    <Layout
      title='Dasboard'
      description={`G'day ${name}!`}
      className='container-fluid'
    >
      <MDBContainer>
        <MDBRow>
          <MDBCol style={{ margin: "5px" }}>{adminInfo()}</MDBCol>
          <div className='w-100' />
          <MDBCol style={{ margin: "5px" }}>{adminLinks()}</MDBCol>
        </MDBRow>
      </MDBContainer>
    </Layout>
  );
};

export default AdminDashboard;
