import React, { useState, useEffect } from "react";
import Layout from "../core/Layout";
import { isAuthenticated } from "../auth/index";
import { Link } from "react-router-dom";
import { createProduct, getCategories } from "./apiAdmin";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBIcon,
  MDBInput,
} from "mdbreact";

const AddProduct = () => {
  const { user, token } = isAuthenticated();

  const [values, setValues] = useState({
    name: "",
    description: "",
    price: "",
    categories: [],
    category: "",
    shipping: "",
    quantity: "",
    photo: "",
    loading: false,
    error: "",
    createdProduct: "",
    redirectToProfile: false,
    formData: "",
  });

  const {
    name,
    description,
    price,
    categories,
    category,
    shipping,
    quantity,
    photo,
    loading,
    error,
    createdProduct,
    redirectToProfile,
    formData,
  } = values;

  //get categories and set form data
  const init = () => {
    getCategories().then((data) => {
      if (data.error) {
        setValues({ ...values, error: data.error });
      } else {
        setValues({ ...values, categories: data, formData: new FormData() });
      }
    });
  };

  useEffect(() => {
    init();
  }, []);

  const handleChange = (name) => (event) => {
    const value = name === "photo" ? event.target.files[0] : event.target.value;
    formData.set(name, value);
    setValues({ ...values, [name]: value });
  };

  const clickSubmit = (event) => {
    event.preventDefault();
    setValues({ ...values, error: "", loading: true });
    createProduct(user._id, token, formData).then((data) => {
      if (data.error) {
        setValues({ ...values, error: data.error });
      } else {
        setValues({
          ...values,
          name: "",
          description: "",
          price: "",
          photo: "",
          category: "",
          quantity: "",
          shipping: "",
          loading: false,
          createdProduct: data.name,
        });
      }
    });
  };

  const newPostForm = () => (
    <MDBContainer>
      <MDBRow center>
        <MDBCol md='6'>
          <MDBCard>
            <MDBCardBody>
              <form onSubmit={clickSubmit}>
                <div className='input-group'>
                  <div className='input-group-prepend'>
                    <span
                      className='input-group-text'
                      id='inputGroupFileAddon01'
                    >
                      <MDBIcon icon='image' size='lg' />
                    </span>
                  </div>
                  <div className='custom-file'>
                    <input
                      type='file'
                      name='photo'
                      accept='image/*'
                      onChange={handleChange("photo")}
                      className='custom-file-input'
                      id='inputGroupFile01'
                      aria-describedby='inputGroupFileAddon01'
                    />
                    <label
                      className='custom-file-label'
                      htmlFor='inputGroupFile01'
                    >
                      Product image ...
                    </label>
                  </div>
                </div>
                <br />
                <MDBInput
                  onChange={handleChange("name")}
                  type='text'
                  rows='2'
                  label='Name'
                  icon='tag'
                  value={name}
                />
                <br />
                <MDBInput
                  onChange={handleChange("description")}
                  type='textarea'
                  rows='2'
                  label='Description'
                  icon='pencil-alt'
                  value={description}
                />
                <br />
                <MDBInput
                  onChange={handleChange("price")}
                  type='number'
                  rows='2'
                  label='Price'
                  icon='dollar-sign'
                  value={price}
                />

                <br />

                <MDBInput
                  onChange={handleChange("quantity")}
                  type='number'
                  rows='2'
                  label='Quantity'
                  icon='align-center'
                  value={quantity}
                />
                <br />
                <div>
                  <MDBIcon icon='layer-group' size='lg' />
                  <label className='ml-3 font-weight-lighter text-muted'>
                    Category
                  </label>
                  <select
                    onChange={handleChange("category")}
                    className='browser-default custom-select'
                  >
                    <option>Please select</option>
                    {categories &&
                      categories.map((c, i) => (
                        <option key={i} value={c._id}>
                          {c.name}
                        </option>
                      ))}
                  </select>
                </div>
                <br />
                <div>
                  <MDBIcon icon='shipping-fast' size='lg' fixed />
                  <label className='ml-2 font-weight-lighter text-muted'>
                    Shipping
                  </label>
                  <select
                    onChange={handleChange("shipping")}
                    className='browser-default custom-select'
                  >
                    <option>Please select</option>
                    <option value='0'>No</option>
                    <option value='1'>Yes</option>
                  </select>
                </div>
                <div className='text-center py-4 mt-3'>
                  <MDBBtn className='btn btn-outline-indigo' type='submit'>
                    Create
                    <MDBIcon icon='plus' className='ml-2' />
                  </MDBBtn>
                </div>
                <div className='mt-5 text-center'>
                  <Link
                    to='/admin/dashboard'
                    className='text-warning'
                    style={{ textDecoration: "underline" }}
                  >
                    Back to Dashboard
                  </Link>
                </div>
              </form>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>
      </MDBRow>
    </MDBContainer>
  );

  const showSuccess = () => (
    <div
      className='alert alert-success'
      style={{ display: createdProduct ? "" : "none" }}
    >
      {createdProduct} product has been created successfully.
    </div>
  );

  const showError = () => (
    <div
      className='alert alert-danger'
      style={{ display: error ? "" : "none" }}
    >
      {error}
    </div>
  );

  const showLoading = () => loading && <MDBIcon icon='spinner' spin />;

  return (
    <Layout
      title='Create A New Product'
      description={`G'day ${user.name}, ready to create a new product`}
      className='container-fluid'
    >
      <MDBContainer>
        {showLoading()}
        {showSuccess()}
        {showError()}
        {newPostForm()}
      </MDBContainer>
    </Layout>
  );
};

export default AddProduct;
