import React, { useState, Fragment } from "react";
import {
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarNav,
  MDBNavItem,
  MDBNavLink,
  MDBNavbarToggler,
  MDBCollapse,
  MDBFormInline,
} from "mdbreact";
import { withRouter } from "react-router-dom";
import { signout, isAuthenticated } from "../auth";

const Menu = ({ history }) => {
  const [open, setOpen] = useState(false);

  const toggleCollapse = () => {
    setOpen(!open);
  };

  const getRole = () => {
    try {
      const {
        user: { role },
      } = isAuthenticated();
      if (role !== undefined) {
        return role;
      }
    } catch (error) {
      if (error) {
        return 0;
      }
    }
  };

  return (
    <MDBNavbar color='indigo' dark expand='md'>
      <MDBNavbarBrand>
          <MDBNavLink to='/'>
            <strong className='white-text'>Logo</strong>
          </MDBNavLink>
      </MDBNavbarBrand>
      <MDBNavbarToggler onClick={toggleCollapse} />
      <MDBCollapse id='navbarCollapse3' isOpen={open} navbar>
        <MDBNavbarNav left>
          <MDBNavItem>
            <MDBNavLink to='/shop'>Shop</MDBNavLink>
          </MDBNavItem>
          <MDBNavItem>
            {getRole() === 1 ? (
              <MDBNavLink to='/admin/dashboard'>Dashboard</MDBNavLink>
            ) : (
              <MDBNavLink to='/user/dashboard'>Dashboard</MDBNavLink>
            )}
          </MDBNavItem>
          {!isAuthenticated() && (
            <Fragment>
              <MDBNavItem>
                <MDBNavLink to='/signup'>Sign up</MDBNavLink>
              </MDBNavItem>
              <MDBNavItem>
                <MDBNavLink to='/signin'>Sign in</MDBNavLink>
              </MDBNavItem>
            </Fragment>
          )}
          {isAuthenticated() && (
            <Fragment>
              <MDBNavItem>
                <MDBNavLink
                  onClick={() =>
                    signout(() => {
                      history.push("/");
                    })
                  }
                  to='/signin'
                >
                  Sign out
                </MDBNavLink>
              </MDBNavItem>
            </Fragment>
          )}
        </MDBNavbarNav>
        <MDBNavbarNav right>
          <MDBNavItem>
            <MDBFormInline waves>
              <div className='md-form my-0'>
                <input
                  className='form-control mr-sm-2'
                  type='text'
                  placeholder='Search Products'
                  aria-label='Search'
                />
              </div>
            </MDBFormInline>
          </MDBNavItem>
        </MDBNavbarNav>
      </MDBCollapse>
    </MDBNavbar>
  );
};

export default withRouter(Menu);
