import React, { useState, useEffect } from "react";
import { MDBListGroupItem } from "mdbreact";

const Checkbox = ({ categories, handleFilters }) => {
  const [checked, setChecked] = useState([]);

  const handleToogle = (c) => () => {
    // returns the first index or -1
    const currentCategoryId = checked.indexOf(c);
    const newCheckedCategoryId = [...checked];
    // if currently checked was not already in checked state > push else we take off
    if (currentCategoryId === -1) {
      newCheckedCategoryId.push(c);
    } else {
      newCheckedCategoryId.splice(currentCategoryId, 1);
    }
    console.log(newCheckedCategoryId);
    setChecked(newCheckedCategoryId);
    handleFilters(newCheckedCategoryId);
  };

  return categories.map((c, i) => (
    <MDBListGroupItem key={i} className='MDBListGroupItemst-unstyled'>
      <div className='custom-control custom-checkbox'>
        <input
          onChange={handleToogle(c._id)}
          type='checkbox'
          className='custom-control-input'
          id={i}
          value={checked.indexOf(c._id === -1)}
        />
        <label className='custom-control-label' htmlFor={i}>
          {c.name}
        </label>
      </div>
    </MDBListGroupItem>
  ));
};

export default Checkbox;
