import React from "react";
import { Route, Redirect } from "react-router-dom";
import { isAuthenticated } from ".";

const privateRoute = ({ component: Component, ...rest }) => (
  <Route
    {...rest}
    render={(props) =>
      isAuthenticated() ? (
        <Component {...props} />
      ) : (
        <Redirect
          to={{
            pathname: "/signin",
            state: { form: props.location },
          }}
        />
      )
    }
  />
);

export default privateRoute;
